// import { Link } from 'gatsby'
import PropTypes from 'prop-types'
import React from 'react'
import styled from 'styled-components'
import { themes } from '../utils/colors'
import Icon from './Icon'
import AppBar from './AppBar'
import css from './Header.module.css'

const theme = themes.teal

// <Title>{headerTitle}</Title>
class Header extends React.Component {
  constructor(props) {
    super(props)

    console.log('header constructor')
    this.state = {
      mainMenu: css.mainMenuClosed,
    }

    this.openMenu = this.openMenu.bind(this)
  }

  openMenu = event => {
    console.log('open menu:', event)
    this.setState({
      mainMenu: css.mainMenuOpen,
    })
  }

  closeMenu = event => {
    console.log('close menu:', event)
    this.setState({
      mainMenu: css.mainMenuClosed,
    })
  }
  handleTouchStart = event => {
    console.log('touch start:', event)
  }

  render() {
    const { headerTitle } = this.props
    return (
      <>
        <header>
          <AppBar headerTitle={headerTitle} />
        </header>
        <MainMenu
          onTouchStart={this.handleTouchStart}
          className={this.state.mainMenu}
        >
          <div id="TopBar">
            <Title>Hovedmeny</Title>
            <Icon name="close" trigger={this.closeMenu} />
          </div>
          <div id="MenuItems">Hello</div>
        </MainMenu>
      </>
    )
  }
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header

const MyHeader = styled.header`
  background-color: ${theme.primary.main};
  color: black;
  top: 0px;
  width: 100%;
  height: 20vw;
  position: fixed;

  div {
    display: flex;
    flex-direction: row;
    padding: 15px 12px;
  }

  & > * {
    margin-right: 12px;
  }
`

const Title = styled.h1`
  text-decoration: none;
  font-size: 1.2em;
  display: inline-block;
  padding-top: 8px;
  padding-left: 32px;
`

const MainMenu = styled.div`
  font-family: 'Roboto';
  position: absolute;
  z-index: 100;
  width: 75vw;
  top: 0px;
  height: 100vh;
  background-color: ${theme.secondary.dark};
  border-color: 1px solid green;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  div#TopBar {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }

  div#MenuItems {
    display: flex;
    flex-grow: 1;
    flex-direction: columns;
    justify-content: flex-start;
    background-color: ${theme.secondary.light};
  }
`
